body,html {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

a-scene {
  z-index: 1;
}
.App {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99;
  /* height: 100vh;
  width: 100vw; */
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: black;
  /* background-image: url('./style.png') */
  
}

.open {
  background-color: transparent;
}

#bg-img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 500px;
  transform: translate(calc(-50% - 130px), calc(-50% + 0px));
  z-index: 0;
}

.text-info {
  font-size: 30px
}



#shape-list-container {
  display: inline-flex;
  position: relative;
  /* position: fixed; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 150px 0 0;
  transform: translate(-80px, 80px);
  /* top: 0;
  left: 0; */
  
  padding: 10px;
  /* width: calc(100% - 20px); */
  /* max-width: 500px; */
  /* height: calc(100vh - 20px); */
  z-index: 99;
}
.frame {
  position: absolute;
  border: 1px solid #00798E;
  width: calc(100% - 50px);
  height: calc(100% - 7px);
  transform: translate(20px, -10px);
}

#shape-list-container > .shape-info {
  display: inline-flex;
  margin: 0 0 15px 0;
  z-index: 10;
  /* border-radius: 20px; */
  /* background-color: aquamarine; */
  /* width: calc(80%);
  height: 50px; */
  /* box-shadow: 1px 1px 3px gray; */
  cursor: pointer;
}

.home-icon{
  position: fixed;
  top: 20px;
  left: 20px;
  width: 25px;
  height: 25px;
  z-index: 99;
  cursor: pointer;
}

.home-icon > img {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .preview-screen{
    display: none;
  }
  #shape-list-container{
    transform: translate(0,0);
    width: calc(100% - 20px);
    margin: 0;
  }
  #shape-list-container > .shape-info{
    width: calc(80%);
    align-items: center
  }
  .frame {
    transform: translate(0px, 0px)
  }
  .shape-info > img {
    width: calc(100%);
    max-width: 280px;
    height: 80px;
  }
}
